body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f3f3ec;
}

ul {
  list-style: none;
  list-style-position: inside;
  padding-left: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  /* background-image: url("https://www.juliusbaer.com/fileadmin/_processed_/0/b/csm_JB_IMG_Places_353_BGL1_RGB_LO_721ab00129.jpg"); */
  /* background-size: cover; */
}

.App-header {
  background-color: white;
  color: #141e55;

  overflow: hidden;
  position: fixed;
  top: 0;
  width: 100%;

  padding-bottom: 10px;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, .5);

  float: left;

  text-transform: uppercase;
}

.Logo {
  height: 30px;
  margin-top: 10px;
}

.App-header-text {
  float:left;
  width: 100%;
  color: #001489;
}

.App-header-help {
  margin: 19px;
  position: absolute; 
}

.App-header h1 {
margin: 2px;
}

.App-header p {
  margin: 0px;
}  

.App-content {
  margin-top: 85px;
}

footer {
  color: gray;
  font-size: small;
  margin-bottom: 10px;
}
.AppList {
    text-align: center;
}


li.AppStore {
    background-color: white;
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 0px 30px 20px -25px #bbb;
    padding: 10px;
    margin-bottom: 15px;
}

.AppStore img {
    width: 100px;
    height: 100px;
    border-radius: 18%;
}

.AppName {
    color: #141e55;
    font-size: x-large;
}

.AppStore p {
    margin-top: 0px;
    margin-bottom: 10px;
}
.AppItems {
    padding: 10px;
    font-size: medium;
}

div .AppItems a {
    /* text-align: center;
    vertical-align: middle;
    display: inline-block;
    align-items:center;Vertical align */
    padding: 5px;
    width: 80%;
    height: 30px;
    background:#141e55;
    border: 1px solid #141e55;
    color: white;
}
 
div .AppItems a:hover {
    background:white;
    color: #141e55;
    text-decoration: none;
  }

div .AppItems a:active {
  text-decoration: none;
    background:white;
    color: #141e55;
}

div .AppItems a:link {
  text-decoration: none;
}

div .AppItems a:visited {
  text-decoration: none;
}

.AppItem {
    padding: 5px;
    border: 1px dotted black;
    margin-bottom: 5px;
}

.AppItem img {
    width: 26px;
    height: 26px;
}

.Platform {
    padding-bottom: 5px;
    vertical-align: middle;
}

.Platform span {
    margin: auto;
}

.AppItem a {
    font-size: large;
}

span {
    padding-left: 12px;
    padding-right: 12px;
    color: white;
    border-radius: 8px;
}

.PROD {    
    background-color: darkred;
}

.UAT {
    background-color: darkorange;
}

.LAB {
    background-color: darkgreen;
}

.DEV {
    background-color: blue;
}

.TST50 {
    background-color: greenyellow;
}
.Close-button {
    /* float: right;
    margin-left: 19px;
    margin-top: -10px; */


    /* overflow: hidden; */
    position: fixed;
    right: 19px;
}

.Help-content {
    clear: both;
    width: 90%;
    margin: auto;
    margin-bottom: 10px;
    font-size: medium;
}

.Help-content p {
    text-align: left;
}

.Help-content img {
    width: 70%;
    box-shadow: 0px 30px 20px -25px #222;
}


select {
    width: 80%;
    height: 25px;
    margin: 8px;
  }

  .AddAppItemPreview {
      width: 80%;
      margin: auto;
      margin-top: 10px;
    }
input {
    width: 80%;
    height: 25px;
    margin: 8px;
  }
 
button {
    width: 80%;
    height: 30px;
    background:#141e55;
    border: 1px solid #141e55;
    color: white;
}
 
button:hover {
    background:white;
    color: #141e55;
}

button:active {
    background:white;
    color: #141e55;
}


button:disabled {
    background:darkgray;
    color: gray;
    border: none;
}

  .Camera button {
      margin: 20px;
  }

